@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{
   
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        width: 100%;

        @include m.desktop { 
            width: auto;
            flex-direction: row;
            
         }

        &__link{
       font-size: 2rem;
       //font-weight: 700;
       color: v.$negro;
       padding: 1.3rem;
       border: .1rem solid v.$primario;


       &:hover{
            color: v.$primario;
        }

        &--registrar{
            background-color: v.$primario;
            color: v.$blanco;
            padding: 1.3rem;
            
            &:hover{
            color: v.$negro;
           
            }
        }
     // &--registrar{
       // @include m.boton(v.$primario, v.$blanco)
         
      //}
      
    }
}
